@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition-duration: .25s;
}

html {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  width: 100vw;
  flex-direction: column;
  align-items: center;
  font-family: Inter;
  display: flex;
  overflow-x: hidden;
}

main {
  width: 100%;
  flex: 1;
}

section {
  width: 100%;
  flex-direction: column;
  align-items: center;
  padding: 40px 16px;
  display: flex;
}

@media (min-width: 768px) {
  section {
    padding: 40px;
  }
}

@media (min-width: 1200px) {
  section {
    padding: 60px 106px;
  }
}

.container {
  width: 100%;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  display: flex;
}

@media (min-width: 768px) {
  .container {
    gap: 32px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1920px;
  }
}

.button {
  text-align: center;
  width: 174px;
  height: 52px;
  cursor: pointer;
  background-color: #ffa800;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.button:focus, .button:hover {
  color: #ffa800;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #ffa800;
  scale: .95;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

a:focus, a:hover {
  color: #ffa800;
}

ul {
  list-style: none;
}

h2 {
  color: #fff;
  font-size: max(36px, min(4vw, 48px));
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero {
  width: 100%;
  height: 100%;
  background-color: #0ff;
  padding: 0;
}

